.main {
    position: relative;
    height: fit-content;
    width: 100vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.outerWrapper {
    width: 100%;
    height: 100%;
    padding: 0 7.5vw 0 7.5vw;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.dark {
    background: var(--oxford-blue);
}

@media (min-width: 580px) and (max-width: 769px) {
    .outerWrapper {
        padding: 0 2.5vw 0 2.5vw;

    }
}

@media (min-width: 400px) and (max-width: 580px) and (max-height: 1000px) {

    .outerWrapper {
        padding: 0 2.5vw 0 2.5vw;
    }
}

@media (max-width: 399px) and (max-height: 900px) {

    .outerWrapper {
        padding: 0 2.5vw 0 2.5vw;
    }
}

.modalOpen {
    overflow: hidden;
}

::-webkit-scrollbar {
width: 1rem;
}

::-webkit-scrollbar-track {
    background-color: var(--lavender-blue);
}

::-webkit-scrollbar-thumb {
    background-color: var(--neon-blue);
}

::-webkit-scrollbar-thumb:hover {
    background-color: #798fff;
}

@supports (scrollbar-color: var(--neon-blue) var(--lavender-blue)) {
    * {
        scrollbar-color: var(--neon-blue) var(--lavender-blue);
        scrollbar-width: thin;
    }
}
