.backdrop {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    backdrop-filter: blur(5px);
    z-index: 20;
}

.backdropLight {
    background-color: #c8cdf95b;
}

.backdropDark {
    background-color: rgba(87, 87, 87, 0.287);
}

.modal {
    position: fixed;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    padding: 2rem 6rem;
    gap: 1.2rem;
    top: 30%;
    left: 0;
    right: 0;
    margin-left: auto;
    margin-right: auto;
    height: 30rem;
    width: 40%;
    min-width: 30rem;
    max-width: 50rem;
    z-index: 21;
    border-radius: 8px;
}

.modalLight {
    background: white;
    box-shadow:
    0px 0px 5.3px rgba(0, 0, 0, 0.25),
    0px 0px 42px rgba(0, 0, 0, 0.5);
}

.modalDark {
    background: var(--oxford-blue);
    box-shadow:
    0px 0px 5.3px rgba(0, 0, 0, 0.25),
    0px 0px 42px rgba(0, 0, 0, 0.5);
    color: var(--cultured);
}


.image {
    width: 6rem;
    height: 6rem;
    fill: #F34F29;
    margin-top: 1rem;
}

.modal h2 {
    font-size: 2.5rem;
}

.modal p {
    text-align: center;
    font-weight: 500;
}

.modalBtn {
    font-family: var(--mono-font);
    font-size: 1.7rem;
    color: var(--dark-text-color);
    font-weight: 600;
    border-radius: 5px;
    outline: none;
    width: 10rem;
    height: 4rem;
    background: inherit;
    cursor: pointer;
}

.modalBtnDark {
    border: 1px solid var(--cultured);
    color: var(--cultured);
}

.modalBtnDark:active {
    background: var(--space-cadet-lightest);
}
.modalBtnLight:active {
    background: var(--antique-white);
}

.modalBtnLight {
    border: 1px solid var(--dark-text-color);
    
}


@media (hover:hover) {
    .modalBtnLight:hover {
        background: var(--antique-white);
    }
    .modalBtnDark:hover {
        background: var(--space-cadet-lightest);
    }
    .backdrop {
        animation: fadeIn .2s;
    }
    .modal{
        animation: fadeInDown .2s;
    }
}

@media (max-width: 768px) and (max-height: 1000px) {
    .modal {
       width: 35rem;
       height: 35rem; 
    }
}
@media (max-width: 400px) and (max-height: 1000px) {
    .modal {
       width: 30rem;
       height: 40rem; 
    }
}
@media (min-width: 768px) and (max-width: 1000px) and (max-height: 768px) {
    .modal {
        top: 10%;
       width: 40rem;
       height: 30rem; 
    }
}


@keyframes fadeInDown {
    0%{
        opacity: 0;
        transform: translateY(-1rem);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes fadeIn {
    0%{
        opacity: 0;
    }
    100%{
        opacity: 1;
    }
}

